import { Route, RouteProps } from 'react-router-dom'
import styles from './index.module.scss'

type Props = RouteProps & {
  activePath: string
}

// 
//  1  activePath: '/home'pathname: '/home'
//  1  activePath: '/home'pathname: '/home/index'
//  2  activePath: '/home'pathname: '/login'
const KeepAlive = ({ activePath, children, ...rest }: Props) => {
  return (
    <Route
      {...rest}
      children={props => {
        const {
          location: { pathname }
        } = props
        const isMatch = pathname.startsWith(activePath)

        return (
          <div
            className={styles.root}
            style={{ display: isMatch ? 'block' : 'none' }}
          >
            {children}
          </div>
        )
      }}
    />
  )
}

export { KeepAlive }
