import { Input, NavBar } from 'antd-mobile'
import classnames from 'classnames'
import io, { Socket } from 'socket.io-client'

import Icon from '../../../components/Icon'

import styles from './index.module.scss'
import { useEffect, useRef, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { baseURL, getToken } from '@/utils'

const Chat = () => {
  const history = useHistory()
  const [value, setValue] = useState('')
  const [chatList, setChatList] = useState<
    { message: string; type: 'user' | 'xz' }[]
  >([])
  const socketRef = useRef<Socket>()
  const chatListRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    //  socket 
    const socketURL = baseURL!.replace('/v1_0', '')
    //  socket 
    const socketio = io(socketURL, {
      query: {
        token: getToken().token,
      },
      transports: ['websocket'],
    })

    // 
    socketio.on('connect', () => {
      // console.log('')
      setChatList([
        { message: '', type: 'xz' },
        { message: '', type: 'xz' },
      ])
    })

    // 
    socketio.on('message', e => {
      // console.log('', e)
      setChatList(list => [
        ...list,
        {
          message: e.msg,
          type: 'xz',
        },
      ])
    })

    //  socketio  ref 
    socketRef.current = socketio
  }, [])

  // 
  useEffect(() => {
    chatListRef.current!.scrollTop = chatListRef.current!.scrollHeight
  }, [chatList])

  // 
  const onSendMsg = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.code !== 'Enter' || value.trim() === '') return

    // 
    socketRef.current?.emit('message', {
      msg: value,
      timestamp: +new Date() + '',
    })

    setChatList([
      ...chatList,
      {
        type: 'user',
        message: value,
      },
    ])

    setValue('')
  }

  return (
    <div className={styles.root}>
      <NavBar className="fixed-header" onBack={() => history.go(-1)}>
        
      </NavBar>

      <div className="chat-list" ref={chatListRef}>
        {chatList.map((item, index) => {
          return (
            <div
              key={index}
              className={classnames(
                'chat-item',
                item.type === 'xz' ? 'self' : 'user'
              )}
            >
              {item.type === 'xz' ? (
                <Icon type="iconbtn_xiaozhitongxue" />
              ) : (
                <img
                  src="http://geek.itheima.net/images/user_head.jpg"
                  alt=""
                />
              )}
              <div className="message">{item.message}</div>
            </div>
          )
        })}
      </div>

      <div className="input-footer">
        <Input
          className="no-border"
          placeholder=""
          value={value}
          onChange={value => setValue(value)}
          onKeyDown={onSendMsg}
        />
        <Icon type="iconbianji" />
      </div>
    </div>
  )
}

export default Chat
