import { useState, useEffect } from 'react'
import { Toast, TextArea, Input, NavBar } from 'antd-mobile'

import styles from './index.module.scss'

type Props = {
  config: {
    text: string
    value: string
  }
  onUpdate: (name: string, value: string | number) => void
  onClose: () => void
}

const EditInput = ({ config, onUpdate, onClose }: Props) => {
  const [value, setValue] = useState(config.value || '')

  useEffect(() => {
    if (config.value !== undefined) {
      setValue(config.value)
    }
  }, [config.value])

  const onUpdateName = () => {
    const name = value.trim()
    if (name === '') return Toast.show('')

    // 
    onUpdate(config.text === '' ? 'name' : 'intro', value)
  }

  return (
    <div className={styles.root}>
      <NavBar
        onBack={onClose}
        className="navbar"
        right={
          <span className="commit-btn" onClick={onUpdateName}>
            
          </span>
        }
      >
        {config.text}
      </NavBar>

      <div className="edit-input-content">
        <h3>{config.text}</h3>

        {config.text === '' ? (
          <div className="input-wrap">
            <Input
              value={value}
              onChange={value => {
                setValue(value)
              }}
              placeholder=""
              maxLength={20}
            />
          </div>
        ) : (
          <TextArea
            className="textarea"
            placeholder=""
            showCount
            maxLength={99}
            value={value}
            onChange={value => setValue(value)}
          />
        )}
      </div>
    </div>
  )
}

export default EditInput
