import { useState, useRef, useEffect } from 'react'
import { useHistory, useLocation } from 'react-router-dom'
import { Toast, NavBar, Input, Form, Button } from 'antd-mobile'

import InputExtra from './components/input-extra'
import classNames from 'classnames'

import { useDispatch } from 'react-redux'
import { sendCode, login } from '@/store/actions'

import styles from './index.module.scss'

import { AxiosError } from 'axios'
import { InputRef } from 'antd-mobile/es/components/input'

type LoginForm = {
  mobile: string
  code: string
}

const Login = () => {
  const [form] = Form.useForm<LoginForm>()
  // location
  //  location.state 
  //      state  undefined
  const location = useLocation<{ from: string } | undefined>()

  //  dispatch 
  const dispatch = useDispatch()

  //  useHistory()  history 
  const history = useHistory()
  //  
  const [time, setTime] = useState(0)
  //  id
  const timerIdRef = useRef(-1)
  //  DOM 
  const mobileRef = useRef<InputRef>(null)

  const [, forceUpdate] = useState({})
  // To disable submit button at the beginning.
  useEffect(() => {
    forceUpdate({})
  }, [])

  // 
  const getCode = async () => {
    //  0 
    //  0 
    if (time > 0) return

    //  mobile  undefined
    const mobile = form.getFieldValue('mobile')
    if (mobile === undefined || form.getFieldError('mobile').length > 0) {
      //  DOM 
      const mobileInputDOM = mobileRef.current
      // focus()  DOM 
      mobileInputDOM?.focus()
      return Toast.show({
        content: '',
        duration: 1000,
      })
    }

    // 60
    setTime(59)
    clearInterval(timerIdRef.current)
    timerIdRef.current = window.setInterval(() => {
      setTime(time => {
        if (time === 1) {
          clearInterval(timerIdRef.current)
        }
        return time - 1
      })
    }, 1000)

    // 
    dispatch(sendCode(mobile))
  }

  const onFinsih = async (values: LoginForm) => {
    try {
      //  await  dispatch action
      // 
      // dispatch 
      await dispatch(login(values))

      // 
      Toast.show({
        content: '',
        duration: 600,
        afterClose: () => {
          // 
          clearInterval(timerIdRef.current)

          //  state 
          if (location.state?.from) {
            // 
            history.replace(location.state.from)
          } else {
            // 
            history.replace('/home')
          }
        },
      })
    } catch (e) {
      //  axios 
      const error = e as AxiosError<{ message: string }>
      // 
      Toast.show({
        content: error.response?.data?.message,
        duration: 1000,
      })
    }
  }

  return (
    <div className={styles.root}>
      {/*  */}
      <NavBar onBack={() => history.go(-1)} />
      <h1 className="title"></h1>

      <div className="login-wrap">
        <Form form={form} onFinish={onFinsih}>
          <Form.Item
            name="mobile"
            rules={[
              { required: true, message: '' },
              {
                pattern: /^1[3-9]\d{9}$/,
                message: '',
              },
            ]}
          >
            <Input placeholder="" ref={mobileRef} />
          </Form.Item>

          <Form.Item
            name="code"
            rules={[
              { required: true, message: '' },
              {
                pattern: /^\d{6}$/,
                message: '',
              },
            ]}
          >
            <InputExtra
              extra={time <= 0 ? '' : `${time}s`}
              onExtra={getCode}
            />
          </Form.Item>
          <Form.Item noStyle shouldUpdate>
            {form => {
              // https://ant-design.gitee.io/components/form-cn/#components-form-demo-inline-login
              // isFieldsTouched(true) 
              // getFieldsError() 
              const disabled =
                !form.isFieldsTouched(true) ||
                form.getFieldsError().filter(({ errors }) => errors.length)
                  .length !== 0

              return (
                <Button
                  block
                  disabled={disabled}
                  type="submit"
                  className={classNames('login-button')}
                >
                  
                </Button>
              )
            }}
          </Form.Item>
          <Form.Item noStyle>
            <div
              style={{
                marginTop: 20,
                textAlign: 'center',
              }}
            >
              246810
            </div>
          </Form.Item>
        </Form>
      </div>
    </div>
  )
}

export default Login
