import classnames from 'classnames'

//  props 
type Props = {
  type: string
  // 
  className?: string
  onClick?: () => void
}

//   props 
const Icon = ({ type, className, onClick }: Props) => {
  return (
    <svg
      className={classnames('icon', className)}
      aria-hidden="true"
      onClick={onClick}
    >
      <use xlinkHref={`#${type}`}></use>
    </svg>
  )
}

export default Icon
