//  as 
// const initialState: LoginState = {} as LoginState
const initialState = {} as Token

//  action 
// type ActionType = {
//   type: string
//   payload: Token
// }

export type LoginType = {
  type: 'login/setToken'
  payload: Token
}

const login = (state = initialState, action: LoginType) => {
  switch (action.type) {
    case 'login/setToken':
      return action.payload
    default:
      return state
  }
}

export { login }
